html {
  box-sizing: inherit; }

*,
*::after,
*::before {
  box-sizing: border-box;
  padding: 0;
  margin: 0; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

body {
  background-color: white;
  min-width: 412px;
  margin: 0 auto; }

ul, li {
  list-style: none; }

.container {
  width: 372px;
  margin: 0 auto; }

.main-nav .main-nav__list {
  display: none;
  transition: 0.4s ease; }

@media (max-width: 767px) {
  .main-nav__toggle {
    width: 60px;
    height: 60px;
    background-color: #ffc200;
    border: none;
    outline: none;
    position: absolute;
    right: 0;
    top: 0; }
  .main-nav__toggle span {
    display: block;
    width: 15px;
    height: 2px;
    background-color: #404041;
    margin: 0 auto;
    margin-bottom: 4px; }
    .main-nav__toggle span:last-of-type {
      margin-bottom: 0; }
  .main-nav--active .main-nav__list {
    display: block; }
  .main-nav__toggle--active span {
    margin-bottom: -2px;
    transition: 0.4 ease; }
    .main-nav__toggle--active span:nth-of-type(1) {
      transform: rotate(-45deg); }
    .main-nav__toggle--active span:nth-of-type(2) {
      display: none; }
    .main-nav__toggle--active span:nth-of-type(3) {
      transform: rotate(45deg); } }

.page-header__wrapper {
  position: relative;
  width: auto;
  margin: 0 auto; }

.page-header__container {
  display: flex;
  padding: 9px 20px 8px 20px;
  align-items: center; }

.page-header__logo {
  margin-right: 56px; }

.page-header__img {
  width: 208px;
  height: 43px; }

.page-header__lang-menu {
  margin-right: auto; }

.site-list__item {
  border-bottom: 1px solid #F5F5F5;
  text-align: center; }

.site-list__link {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 18px;
  line-height: 18px;
  font-weight: 400;
  color: #949494;
  padding: 13px 20px 18px 20px;
  display: block; }

.lang-menu {
  display: none;
  transition: 0.4 ease; }

.lang-menu--active {
  display: block; }

.lang-menu__item {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 12px;
  line-height: 21px;
  font-weight: 500;
  color: #404041;
  border-bottom: 1px solid #707070; }
  .lang-menu__item:last-of-type {
    border-bottom: none; }
  .lang-menu__item--active {
    color: #A8A8A8; }

.user-list {
  border-top: 1px solid #F5F5F5;
  border-bottom: 1px solid #F5F5F5;
  padding: 20px;
  margin: 0 auto;
  text-align: center; }

.user-list__item {
  margin: 15px 0; }

.user-list__link {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 16px;
  font-weight: 500;
  color: #262626;
  padding: 13px 21px 16px 20px;
  border: 1px solid #ffc200;
  border-radius: 25px;
  transition: 0.4s ease; }
  .user-list__link:hover, .user-list__link:focus {
    background-color: #ffc200; }
  .user-list__link:active {
    background-color: #585858;
    border-color: #585858;
    color: #ffffff; }

.promo {
  background: url("../img/main-bg-mob.png") center center/cover no-repeat #FFC200; }
  @media (min-resolution: 1.5dppx) {
    .promo {
      background-image: url("../img/main-bg-mob@2x.png"); } }

.promo__wrapper {
  padding-top: 50px;
  padding-bottom: 15px; }

.credit-features__title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 38px;
  line-height: 46px;
  font-weight: 800;
  text-transform: uppercase;
  color: #262626;
  margin-bottom: 19px; }
  .credit-features__title span {
    font-weight: 400; }

.credit-features__list {
  margin-bottom: 42px; }

.credit-features__item {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 30px;
  font-weight: 500;
  color: #404041;
  padding-left: 34px;
  position: relative; }
  .credit-features__item::before {
    content: "";
    position: absolute;
    width: 12px;
    height: 8px;
    border-left: 2px solid #ffffff;
    border-bottom: 2px solid #ffffff;
    left: 0;
    top: 50%;
    transform: translateY(-50%) rotate(-45deg); }

.credit-range {
  height: 529px; }

.credit-nav__list {
  display: flex;
  width: 100%; }

.credit-nav__item {
  margin-right: 17px; }
  .credit-nav__item:nth-of-type(3n) {
